
.ion-popover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.popover-wrapper {
  z-index: 10;
}
.popover-content {
  position: absolute;
  z-index: 10;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  overflow: auto;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
}
.popover-content .ion-content,
.popover-content .scroll-content {
  contain: none;
}
.popover-content .scroll-content {
  position: relative;
}
.popover-ios .popover-content {
  width: 200px;
  min-width: 0;
  min-height: 0;
  max-height: 90%;
  border-radius: 10px;
  color: #000;
  background: #fff;
}
.popover-ios .popover-arrow {
  position: absolute;
  display: block;
  overflow: hidden;
  width: 20px;
  height: 10px;
}
.popover-ios .popover-arrow::after {
  position: absolute;
  top: 3px;
  left: 3px;
  z-index: 10;
  width: 14px;
  height: 14px;
  border-radius: 3px;
  background-color: #fff;
  content: "";
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.popover-ios.popover-bottom .popover-arrow {
  top: auto;
  bottom: -10px;
}
.popover-ios.popover-bottom .popover-arrow::after {
  top: -6px;
}
.popover-md .popover-content {
  width: 250px;
  min-width: 0;
  min-height: 0;
  max-height: 90%;
  border-radius: 2px;
  color: #000;
  background: #fff;
