  -webkit-transform-origin: top left;
          transform-origin: top left;
}
.popover-md .popover-viewport {
  -webkit-transition-delay: 100ms;
          transition-delay: 100ms;
}
.popover-ios-enter-active, .popove-ios-leave-active {
  -webkit-transition: opacity ease .1s;
  transition: opacity ease .1s;
  opacity: 1;
}
.popover-ios-enter, .popover-ios-leave-active {
  opacity: 0;
  -webkit-transition: opacity ease .1s;
  transition: opacity ease .1s;
}
.popover-md .popover-wrapper {
  -webkit-transition: opacity ease 100ms;
  transition: opacity ease 100ms;
}
.popover-md .popover-wrapper .popover-content {
    -webkit-transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 300ms;
    transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 300ms;
}
.popover-md .popover-wrapper .popover-content .popover-viewport {
      -webkit-transition: opacity ease 300ms;
      transition: opacity ease 300ms;
}
.popover-md-enter {
  opacity: 1;
}
.popover-md-enter .popover-content {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.popover-md-enter .popover-viewport {
    opacity: 1;
}
.popover-md-enter-active {
  opacity: 0;
}
.popover-md-enter-active .popover-content {
    -webkit-transform: scale(0);
            transform: scale(0);
}
.popover-md-enter-active .popover-viewport {
    opacity: 0;
}
.popover-md-leave-active {
  opacity: 0;
}



