
.ion-icon {
  display: inline-block;
  font-size: 1.2em;
  min-width: 18px;
  text-align: center;
}
.ion-icon[small] {
  min-height: 1.1em;
  font-size: 1.1em;
}
.icon-primary {
  color: #387ef5;
}
.icon-secondary {
  color: #32db64;
}
.icon-danger {
  color: #f53d3d;
}
.icon-light {
  color: #f4f4f4;
}
.icon-dark {
  color: #222;
}



