
.action-sheet {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: block;
  width: 100%;
  height: 100%;
}
.action-sheet-wrapper {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: block;
  margin: auto;
  width: 100%;
  max-width: 500px;
  -webkit-transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
  transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
}
.action-sheet-button {
  width: 100%;
}
.action-sheet-ios {
  text-align: center;
}
.action-sheet-ios .action-sheet-container {
  padding: 0 10px;
}
.action-sheet-ios .action-sheet-group {
  overflow: hidden;
  margin-bottom: 8px;
  border-radius: 13px;
  background: #f9f9f9;
}
.action-sheet-ios .action-sheet-group .action-sheet-buttons {
    max-height: 258px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.action-sheet-ios .action-sheet-group:last-child {
  margin-bottom: 10px;
}
.action-sheet-ios .action-sheet-title {
  padding: 15px;
  border-bottom: 0.55px solid #d6d6da;
  border-radius: 0;
  font-size: 13px;
  font-weight: 400;
  text-align: center;
  color: #8f8f8f;
}
.action-sheet-ios .action-sheet-title .action-sheet-sub-title {
    margin-top: 3%;
    font-size: 77%;
    line-height: 130%;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
}
.action-sheet-ios .action-sheet-button {
  margin: 0;
  padding: 18px;
  min-height: 56px;
  border-bottom: 0.55px solid #d6d6da;
  font-size: 20px;
  color: #007aff;
  background: transparent;
}
.action-sheet-ios .action-sheet-button:last-child {
  border-bottom-color: transparent;
}
.action-sheet-ios .action-sheet-button:active {
  border-bottom-color: #ebebeb;
  background: #ebebeb;
}
.action-sheet-ios .action-sheet-selected {
  font-weight: bold;
  background: #fff;
}
.action-sheet-ios .action-sheet-destructive {
  color: #f53d3d;
}
.action-sheet-ios .action-sheet-cancel {
  font-weight: 600;
  background: #fff;
}
.action-sheet-md .action-sheet-container {
  padding: 8px 0;
  background: #fafafa;
}
.action-sheet-md .action-sheet-title {
  padding: 11px 16px 17px;
  font-size: 16px;
  text-align: left;
  color: #757575;
}
.action-sheet-md .action-sheet-title .action-sheet-sub-title {
    margin-top: 2%;
    font-size: 77%;
    line-height: 130%;
    max-width: 90%;
    margin-left: 0;
    margin-right: auto;
}
.action-sheet-md .action-sheet-button {
  position: relative;
  overflow: hidden;
  padding: 0 16px;
  min-height: 48px;
  font-size: 16px;
  text-align: left;
  color: #222;
  background: transparent;
}
.action-sheet-md .action-sheet-button:active {
  background: #f1f1f1;
}
.action-sheet-md .action-sheet-icon {
  margin: 0 32px 0 0;
  padding: 0;
  width: 23px;
  font-size: 24px;
  text-align: center;
  vertical-align: middle;
}
.action-sheet-md .action-sheet-group {
  overflow: hidden;
}
.action-sheet-md .action-sheet-group .action-sheet-buttons {
    max-height: 265px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
}
.action-sheet-md .action-sheet-group .button-inner {
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.action-sheet-md .action-sheet-selected {
  font-weight: bold;
}
.action-sheet-enter-active, .action-sheet-leave-active {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
.action-sheet-enter, .action-sheet-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}



