
.ion-header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  display: block;
  width: 100%;
  transition: -webkit-transform ease 300ms;
}
.ion-header.hide-bar {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}



