
.ion-loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
      -ms-flex-pack: center;
          justify-content: center;
  contain: strict;
}
.loading-wrapper {
  z-index: 10;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  opacity: 1;
  font-size: 14px;
}
.loading-ios .loading-wrapper {
  padding: 24px 34px;
  max-width: 270px;
  max-height: 90%;
  border-radius: 8px;
  color: #000;
  background: #f8f8f8;
  margin-bottom: 44px;
  -webkit-transition: all ease-in-out 200ms;
  transition: all ease-in-out 200ms;
}
.loading-ios .loading-content {
  font-weight: bold;
}
.loading-ios .loading-spinner + .loading-content {
  margin-left: 16px;
}
.loading-ios .spinner-ios line,
.loading-ios .spinner-ios-small line {
  stroke: #69717d;
}
.loading-ios .spinner-bubbles circle {
  fill: #69717d;
}
.loading-ios .spinner-circles circle {
  fill: #69717d;
}
.loading-ios .spinner-crescent circle {
  stroke: #69717d;
}
.loading-ios .spinner-dots circle {
  fill: #69717d;
}
.loading-ios-enter-active, .loading-ios-leave-active {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.loading-ios-enter {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 0.01;
}
.loading-ios-leave-active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0;
}
.loading-md .loading-wrapper {
  padding: 24px;
  max-width: 280px;
  max-height: 90%;
  border-radius: 2px;
  color: rgba(0, 0, 0, 0.5);
  background: #fafafa;
  box-shadow: 0 16px 20px rgba(0, 0, 0, 0.4);
  -webkit-transition: all ease-in-out 200ms;
  transition: all ease-in-out 200ms;
}
.loading-md .loading-spinner + .loading-content {
  margin-left: 16px;
}
.loading-md .spinner-ios line,
.loading-md .spinner-ios-small line {
  stroke: #327eff;
}
.loading-md .spinner-bubbles circle {
  fill: #327eff;
}
.loading-md .spinner-circles circle {
  fill: #327eff;
}
.loading-md .spinner-crescent circle {
  stroke: #327eff;
}
.loading-md .spinner-dots circle {
  fill: #327eff;
}
.loading-md-enter-active, .loading-md-leave-active {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.loading-md-enter {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  opacity: 0;
}
.loading-md-leave-active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0;
}
.indicator .loading-wrapper {
  background: rgba(0, 0, 0, 0.9);
  color: #fff;
  padding: 13px 15px;
}
.indicator .loading-wrapper circle, .indicator .loading-wrapper line {
    stroke: #fff !important;
}



