
.ion-page {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 10;
}
.ion-page.show-page {
  opacity: 1;
}
.ios .ion-page ~ .nav-decor {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: #000;
  pointer-events: none;
}
.ios .ion-page.page-top {
  z-index: 15 !important;
}
.ios .ion-page.page-bottom {
  z-index: 5 !important;
}



