
.ion-menu {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  opacity: 1;
  display: none;
  contain: strict;
}
.ion-menu.show-menu {
  display: block;
}
.menu-inner {
  position: absolute;
  top: 0;
  right: auto;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: block;
  width: 304px;
  height: 100%;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all cubic-bezier(0, 0, 0, 1) 280ms;
  transition: all cubic-bezier(0, 0, 0, 1) 280ms;
  contain: strict;
}
.menu-inner > .ion-header,
.menu-inner > .ion-content,
.menu-inner > .ion-footer {
  position: absolute;
}
.ion-menu[side=right] > .menu-inner {
  right: 0;
  left: auto;
}
.ion-menu .ion-backdrop {
  z-index: -1;
}
.menu-content {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: all cubic-bezier(0, 0, 0.6, 1) 280ms;
  transition: all cubic-bezier(0, 0, 0.6, 1) 280ms;
}
.menu-content-open {
  cursor: pointer;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
.menu-content-open .ion-pane,
.menu-content-open .ion-content,
.menu-content-open .toolbar {
  pointer-events: none;
}
@media (max-width: 340px) {
.menu-inner {
    width: 264px;
}
}
.ion-menu[type=reveal].show-menu {
  z-index: 0;
}
.ion-menu[type=reveal] .menu-inner {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.ion-menu[type=reveal].show-menu .menu-inner {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.ion-menu[type=overlay].show-menu {
  z-index: 1;
}
.ion-menu[type=push].show-menu {
  z-index: 0;
}
.ios .menu-inner {
  background: #fff;
}
.ios .menu-content-reveal {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}
.ios .menu-content-push {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}
.ios .ion-menu[type=overlay] .menu-inner {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}
.md .menu-inner {
  background: #fff;
}
.md .menu-content-reveal {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}
.md .menu-content-push {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}
.md .ion-menu[type=overlay] .menu-inner {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

/*slideInLeft*/
/*animate class*/
.slideInLeft-enter-active,
.slideInLeft-leave-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.slideInLeft-enter,
.slideInLeft-leave-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: all cubic-bezier(0.2, 0, 1, 1) 280ms;
  transition: all cubic-bezier(0.2, 0, 1, 1) 280ms;
}

/*slideInRight*/
/*animate class*/
.slideInRight-enter-active,
.slideInRight-leave-active {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.slideInRight-enter,
.slideInRight-leave-active {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-transition: all cubic-bezier(0, 0, 1, 1) 280ms;
  transition: all cubic-bezier(0, 0, 1, 1) 280ms;
}



