
.ion-content {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  contain: layout size style;
}
.ion-page > .ion-content {
  position: absolute;
}
a {
  color: #387ef5;
}
.ion-content.js-scroll > .scroll-content {
  position: absolute;
  z-index: 1;
  -webkit-tap-highlight-color: transparent;
  width: 100%;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  -o-text-size-adjust: none;
  text-size-adjust: none;
  top: inherit;
  right: inherit;
  bottom: inherit;
  left: inherit;
  contain: content;
  overflow: hidden;
  min-height: 100%;
}
.scroll-content {
  position: absolute;
  top: 1px;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: block;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  contain: size style layout;
}
.disable-scroll .ion-page {
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
}
.fixed-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
}
.fixed-content [fixed] {
  position: absolute;
  z-index: 999;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.fixed-content [fixedTop] {
  width: 100%;
  top: 0;
}
.fixed-content [fixedBottom] {
  bottom: 0;
  width: 100%;
}
.ion-app [no-padding],
.ion-app [no-padding] .scroll-content {
  padding: 0;
}
.ion-app [padding],
.ion-app [padding] .scroll-content {
  padding: 16px;
}
.content-ios {
  color: #000;
  background-color: #fff;
}
.content-ios.outer-content {
  background: #efeff4;
}
.content-ios hr {
  height: 0.55px;
  background-color: rgba(0, 0, 0, 0.12);
}
.content-ios:not([no-bounce]) > .scroll-content::before,
.content-ios:not([no-bounce]) > .scroll-content::after {
  position: absolute;
  width: 1px;
  /*no*/
  height: 1px;
  /*no*/
  content: "";
}
.content-ios:not([no-bounce]) > .scroll-content::before {
  bottom: -1px;
  /*no*/
}
.content-ios:not([no-bounce]) > .scroll-content::after {
  top: -1px;
  /*no*/
}
.content-md {
  color: #000;
  background-color: #fff;
}
.content-md hr {
  background-color: rgba(0, 0, 0, 0.08);
}
.content-md.outer-content {
  background: #fff;
}



