
.ion-modal {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  contain: strict;
  z-index: 12;
}
@media not all and (min-width: 768px) and (min-height: 600px) {
.ion-modal .ion-backdrop {
    visibility: hidden;
}
}
.modal-wrapper {
  z-index: 10;
  height: 100%;
  contain: strict;
}
@media only screen and (min-width: 768px) and (min-height: 600px) {
.modal-wrapper {
      position: absolute;
      top: -webkit-calc(50% - (500px / 2));
      top: calc(50% - (500px / 2));
      left: -webkit-calc(50% - (600px / 2));
      left: calc(50% - (600px / 2));
      width: 600px;
      height: 500px;
}
}
@media only screen and (min-width: 768px) and (min-height: 768px) {
.modal-wrapper {
      position: absolute;
      top: -webkit-calc(50% - (600px / 2));
      top: calc(50% - (600px / 2));
      left: -webkit-calc(50% - (600px / 2));
      left: calc(50% - (600px / 2));
      width: 600px;
      height: 600px;
}
}
.ios .modal-wrapper {
  -webkit-transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
  transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
}
@media only screen and (min-width: 768px) and (min-height: 600px) {
.ios .modal-wrapper {
      overflow: hidden;
      border-radius: 10px;
}
}
.modal-ios-enter-active, .modal-ios-leave-active {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  opacity: 1;
}
.modal-ios-enter, .modal-ios-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  opacity: 0;
}
.md .modal-wrapper {
  -webkit-transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
  transition: all cubic-bezier(0.36, 0.66, 0.04, 1) 400ms;
}
@media only screen and (min-width: 768px) and (min-height: 600px) {
.md .modal-wrapper {
      overflow: hidden;
      border-radius: 2px;
      box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
}
}
.modal-md-enter-active, .modal-md-leave-active {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 1;
}
.modal-md-enter, .modal-md-leave-active {
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
  opacity: 0.01;
}
.modal-zoom-enter-active, .modal-zoom-leave-active {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.modal-zoom-enter, .modal-zoom-leave-active {
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
  opacity: 0.01;
}



